//local imports
import './Logo1.css';

const Logo1 = () => {
    return (
        <div className='logo1'>
            <img 
                alt='app logo img' 
                src='/assets/images/logo/logo1.png'
                className='logo1-big'
            />
            <img 
                alt='app logo img' 
                src='/assets/images/logo/logo1-small.png'
                className='logo1-small'
            />
        </div>
    )
}

export default Logo1;
